import React,{Component} from 'react'
import {Breadcrumb,Form,Button,FloatingLabel,Row,Col} from 'react-bootstrap';
import { Link } from 'react-router-dom';


class contact extends Component{
    constructor(props){
        super(props);
        this.state={};
    }
    render(){
        return(
            <>
                <Breadcrumb className="bread-crumb">
                    <Breadcrumb.Item className="bread-item"><Link to="/home">Home</Link></Breadcrumb.Item>
                    <Breadcrumb.Item className="bread-item"><Link to="/contact">Contact</Link></Breadcrumb.Item>
                </Breadcrumb>

                <div className="form container">
                    <h3 className="form-header">ENQUERY</h3><hr />

                    <Form>

                        <Form.Group as={Row} className="mb-3" controlId="formHorizontalText">
                            <Form.Label column sm={2}><h6>Name</h6></Form.Label>
                            <Col sm={10}><Form.Control type="text" placeholder="Name" /></Col>
                        </Form.Group>

                        <Form.Group as={Row} className="mb-3" controlId="formHorizontalEmail">
                            <Form.Label column sm={2}><h6>Email</h6></Form.Label>
                            <Col sm={10}> <Form.Control type="email" placeholder="Email" /></Col>
                        </Form.Group>

                        <Form.Group as={Row} className="mb-3" controlId="formHorizontalNumber">
                            <Form.Label column sm={2}><h6>Tel No.</h6></Form.Label>
                            <Col sm={10}> <Form.Control type="number" placeholder="Tel no." /></Col>
                        </Form.Group>

                        <Form.Group as={Row} className="mb-3" controlId="formHorizontalNumber">
                            <Form.Label column sm={2}><h6>Query</h6></Form.Label>
                            <Col sm={10}><Form.Control as="textarea" placeholder='Query' rows={3} /></Col>
                        </Form.Group>

                        <fieldset>
                            <Form.Group as={Row} className="mb-3">
                                <Form.Label as="legend" column sm={2}>
                                    <h6>Contact type</h6>
                                </Form.Label>

                                <Col sm={10}>
                                <Form.Select aria-label="Default select example">
                                    <option value="1">E-Mail</option>
                                    <option value="2">Call</option>
                                    <option value="3">Message</option>
                                    </Form.Select>
                                </Col>
                            </Form.Group>
                        </fieldset>

                        <Form.Group className="mb-3" controlId="formBasicCheckbox" Id="">
                            <Form.Check type="checkbox" label="agreed to terms and condition" />
                        </Form.Group>

                        <Button variant="primary lg" Id="formBasicSubmit" type="submit">
                            Submit
                        </Button>
                        <br/>
                        <br/>
                    </Form>
                </div>
            </>
        );
    }
}
export default contact;